import {BookmarkValue} from './bookmark-value';

/**
 * Bookmark
 */
export class Bookmark implements Validation {

    /**
     * BookmarkID
     * IDputdeleteURI.
     */
    id : number;

    /**
     * 
     * null.
     */
    parent : Bookmark;

    /**
     * 
     * 
     */
    title : string;

    /**
     * 
     * 
     */
    detail : string;

    /**
     * URL
     * .
     */
    url : string;

    /**
     * 
     */
    regDate : Date;

    /**
     * 
     * TRUE
     */
    folder : boolean;

    /**
     * .
     * null
     */
    bookmark : Bookmark[];

    /**
     * Bookmark.
     *
     * @param folder true
     * @param id ID
     * @param parentId ID
     */
    constructor (folder: boolean , id: number , parent: Bookmark) {
        this.id = id;
        this.parent = parent;
        this.title = '';
        this.detail = '';
        this.url = '';
        this.regDate = new Date();
        this.folder = folder;
        this.bookmark = (folder) ? new Array() : null;
    }

    /**
     * Bookmark.
     * @returns {string}
     */
    toString() : string {
        // JSON
        return JSON.stringify(this);
    };

    /**
     * BookmarkJSON.
     * @param rootBM BookmarkRoot
     * @returns {string}
     */
    static toJSON(rootBM: Bookmark) : string {
        // JSON.
        const bookmarkValues = BookmarkValue.fromBM(rootBM);
        // JSON.
        return BookmarkValue.toJSON(bookmarkValues);
    };

    /**
     * JSONBookmark.
     * @param jsonStr
     */
    static fromJSON(jsonStr: string) : Bookmark {
        // JSON.
        const bookmarkValues = BookmarkValue.fromJSON(jsonStr);
        // 
        return BookmarkValue.toBM(bookmarkValues);
    }

    /**
     * .
     * @returns {boolean}
     */
    validate() {
        //TODO: .
        return true;
    }

    /**
     * Bookmark.
     * .
     *
     * @return {number} .
     */
    getSize() : number {
        if (this.folder === false) {
            return 1;
        }
        return this.bookmark
        .map( (bm: Bookmark) => { return bm.getSize(); })
        .reduce((x: number, y: number) => { return x + y; }, 1);
    }

    /**
     * .
     */
    getRoot() : Bookmark {
        var bm : Bookmark = this;
        // .
        // : parentnull
        while (bm.parent !== null) {
            bm = this.parent;
        }
        return bm;
    }

    /**
     * BM.
     *
     * BMparentnull.
     *
     * BMparent
     * parentidrootparent.
     *
     * @param bm BM.
     *
     */
    addChild(bm: Bookmark) : void {

        // parent
        if (!bm.parent) {
            bm.parent = this;
            this.bookmark.push(bm);
            return;
        }

        // parent
        if (bm.parent === this) {
            this.bookmark.push(bm);
        } else {
            const parent = this.searchAll(bm.parent.id);
            if (parent) {
                parent.bookmark.push(bm);
            } else {
                throw Error(':' + bm.parent.id + 'ID.');
            }
        }
    }

    /**
     * 
     */
    remove() : void {
        if (!this.parent) {
            throw Error(': root.');
        }
        const index = this.parent.bookmark.indexOf(this);
        this.parent.bookmark.splice(index, 1);

        this.parent = null;
    }

    /**
     * ID.
     * undefined.
     * @param id ID
     *
     * @return Bookmark
     */
    search(id: number) : Bookmark {

        // 
        if ( this.id === id ) {
            return this;
        }


        // 
        if (this.folder === false) {
            return undefined;
        }

        // 
        if (this.bookmark) {
            for (var i = 0; i < this.bookmark.length; i++) {
                // 
                var ret = this.bookmark[i].search(id);
                if (ret) {
                    return ret;
                }
            }
        }

        return undefined;
    }

    /**
     * root
     */
    getHierarchy() : Bookmark[] {
        const hierarchy : Bookmark[] = [];
        var tmp : Bookmark = this;
        // root.
        do {
            hierarchy.push(tmp);
        }while ((tmp = (tmp.parent)) !== null);

        // [0] root -> ... -> this .
        return hierarchy.reverse();
    }

    /**
     * BookmarkID
     * undefined.
     * @param id
     */
    searchAll(id: number) : Bookmark {
        return this.getRoot().search(id);
    }

}



